export default {
	data() {
		return {
			notes:[],
		}
	},
	onShow() {
		this.getNotes()
	},
	methods: {
		// 获取注意事项信息接口
		async getNotes() {
			try {
				let data = await this.$wxhttp.post({
					url: '/report/v1/getNotes'
				})
				this.notes = data.data;
			} catch (e) {
				console.log(e)
			}
		},
	}
}
